.container {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center
}

.container.bottom {
        flex-direction: column
    }

.container.bottom .label {
            margin-top: 1.5rem;
        }

.container.top {
        flex-direction: column-reverse
    }

.container.top .label {
            margin-bottom: 1.5rem;
        }

.container.right {
        flex-direction: row
    }

.container.right .label {
            margin-left: 2rem;
        }

.container.left {
        flex-direction: row-reverse
    }

.container.left .label {
            margin-right: 2rem;
        }

.container .hidden {
        display: none;
    }

.container .label {
        opacity: 0.5;
        color: #334a43;
        font-family: "IbmPlexMono", monospace;
        font-weight: 400;
        font-size: 1.2rem;
        line-height: calc(2em / 1.2);
        transition: opacity 0.2s ease;
    }

.container .circleWrapper {
        position: relative
    }

.container .circleWrapper .circle::after,
        .container .circleWrapper .avatarPlaceholder::after {
            transition: background-color 0.2s ease;
        }

.container .circleWrapper .imageOverlay {
            will-change: opacity;
            position: relative;
            width: 100%;
            height: 100%;
            overflow: hidden;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background-color: rgba(51, 74, 67, 0.8);
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 10;
            opacity: 0;
            font-size: 2rem;
            transition: opacity 0.2s ease
        }

.container .circleWrapper .imageOverlay .icon {
                font-size: 1.5em;
                fill: #fff;
            }

.container .circleWrapper .smallCircle {
            position: relative;
            width: 1.9em;
            height: 1.9em;
            overflow: hidden;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background-color: #fff;
            border-radius: 50%;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 10;
            margin: 0.1rem;
            border: 1px solid rgba(51, 74, 67, 0.2);
            font-size: 1rem
        }

.container .circleWrapper .smallCircle .icon {
                font-size: 1.5em;
                fill: #334a43;
            }

.container .circleWrapper .avatarPlaceholder {
            position: relative;
            width: 4em;
            height: 4em;
            overflow: hidden;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background-color: #fff;
            border-radius: 50%;
            font-size: 2rem
        }

.container .circleWrapper .avatarPlaceholder::after {
                position: relative;
                width: 4em;
                height: 4em;
                overflow: hidden;
                display: inline-flex;
                justify-content: center;
                align-items: center;
                background-color: rgba(51, 74, 67, 0.2);
                border-radius: 50%;
                content: "";
                position: absolute;
                top: 0;
                left: 0;
            }

.container .circleWrapper .avatarPlaceholder .icon {
                font-size: 1.5em;
                fill: #334a43;
                fill-opacity: 0.8;
            }

.container:hover {
        cursor: pointer
    }

.container:hover .circleWrapper .circle::after {
                background-color: rgba(51, 74, 67, 0.4);
            }

.container:hover .circleWrapper .avatarPlaceholder::after {
                background-color: rgba(51, 74, 67, 0.4);
            }

.container:hover .circleWrapper .imageOverlay {
                opacity: 1;
            }

.container:hover .label {
            opacity: 1;
        }
