.option {
    will-change: background-color;
    padding: 0.7rem 1.5rem;
    background-color: #fff;
    color: #4b453d;
    cursor: pointer;
    transition: background-color 0.2s ease
}

.option.focused {
        background-color: #f3ece5;
    }
