/* ==========================================================================
  Fonts
  ========================================================================== */

/* ==========================================================================
  All typography variants

  $unit - Either em or rem
  ========================================================================== */

.container {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
}

.input {
    display: none
}

.input:checked + .circle {
        border-color: #d4b58b;
        background-color: #d4b58b
    }

.input:checked + .circle .badge {
            display: flex;
        }

.input:checked + .circle + span {
            color: rgb(75, 69, 61);
        }

.label {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "IbmPlexMono", monospace;
    font-weight: 400;
    font-size: 1.2rem;
    line-height: calc(2em / 1.2);
    color: rgba(75, 69, 61, 0.5);
    transition: color 0.2s ease
}

.label.selectable:hover {
        color: rgb(75, 69, 61);
        cursor: pointer
    }

.label.selectable:hover .circle {
            border-color: rgba(51, 74, 67, 0.5);
        }

.label.selectable:hover .input:checked + .circle {
                border-color: rgba(212, 181, 139, 0.8);
                background-color: rgba(212, 181, 139, 0.8);
            }

.label.noLabel .circle {
        margin-bottom: 0;
    }

.circle {
    position: relative;
    width: 4em;
    height: 4em;
    overflow: hidden;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border-radius: 50%;
    will-change: background-color, border-color;
    overflow: visible;
    margin-bottom: 1.5rem;
    border: 1px solid rgba(51, 74, 67, 0.2);
    color: #4b453d;
    font-size: 2rem;
    transition:
        background-color 0.2s ease,
        border-color 0.2s ease;
    fill: #4b453d
}

.circle > i {
        font-size: 1.5em;
    }

.badge {
    position: relative;
    width: 1.9em;
    height: 1.9em;
    overflow: hidden;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    top: 0;
    right: 0;
    margin: 0.1rem;
    display: none;
    border: 1px solid #d4b58b;
    color: #334a43;
    font-size: 1rem
}

.badge > i {
        font-size: 1em;
    }
