/*
  Note: If you add a color here, please add it to "/stories/base/colors.css" as well
*/

.modal {
    height: 100%;
    margin: 0 auto;
    padding: 2.5rem;
    display: table;
    outline: none
}

.modal .modalCenter {
        position: relative;
        display: table-cell;
        vertical-align: middle;
    }

.modal .modalContent {
        will-change: transform;
        transform: scaleX(0);
        transform-origin: left;
        background: #fff;
        transition: transform 0.25s cubic-bezier(0.215, 0.61, 0.355, 1);
    }

.modal.afterOpen .modalContent {
        transform: scaleX(1);
        transition-delay: 0.2s;
    }

.modal.beforeClose .modalContent {
        transform: scaleX(0);
        transition-delay: 0s;
    }

.modalOverlay {
    will-change: opacity;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    min-width: 102.4rem;
    min-height: 70rem;
    opacity: 0;
    transition: opacity 0.2s linear
}

.modalOverlay::after {
        content: "";
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
        opacity: 0.95;
        background: url("../../media/images/half-circle-light-pattern-2x.jpg");
        background-size: 1.6rem 1.6rem;
        background-repeat: repeat;
    }

.modalOverlay.afterOpen {
        opacity: 1;
        transition-delay: 0s;
    }

.modalOverlay.beforeClose {
        opacity: 0;
        transition-delay: 0.25s;
    }

.modalPortal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999999;
    overflow-x: auto;
    overflow-y: scroll;
    display: none
}

.modalPortal:not(:empty) {
        display: block;
    }

.modalBodyOpen {
    /* Alongside this, `padding-right` equal to the sidebar width will be added */
    /* See: https://github.com/srph/scrollbar-compensate */
    overflow: hidden;
}
