/* ==========================================================================
  All typography variants

  $unit - Either em or rem
  ========================================================================== */

.badge {
    font-family: "IbmPlexMono", monospace;
    font-weight: 400;
    font-size: 1.2rem;
    line-height: calc(2em / 1.2);
    position: relative;
    min-width: 6.5rem;
    height: 4rem;
    overflow: hidden;
    padding: 0 1.5rem;
    border: none;
    border-width: 0.1rem;
    border-style: solid;
    border-color: rgba(51, 74, 67, 0.2);
    outline: 0;
    background-color: #fff;
    border-radius: 2rem;
    box-shadow: inset 0 0 0 1rem #fff;
    color: #4b453d;
    text-overflow: ellipse;
    transition-property: background-color, color, border-color, box-shadow;
    transition-duration: 0.2s;
    transition-timing-function: cubic-bezier(0.4, 0, 0.6, 1)
}

.badge.hide-overflow {
        max-width: 15.44rem;
    }

.badge:hover {
        border-color: rgba(51, 74, 67, 0.5);
    }

.badge:focus {
        border-color: rgba(51, 74, 67, 0.5);
    }

.badge div {
        overflow: hidden;
        text-overflow: ellipsis
    }

.badge div span {
            white-space: nowrap;
        }

.badge:enabled {
        cursor: pointer
    }

.badge:enabled.selected {
            border-color: transparent;
            background-color: #d4b58b;
            box-shadow: inset 0 0 0 0 #fff;
            color: #fff
        }

.badge:enabled.selected:hover {
                background-color: rgba(212, 181, 139, 0.8);
            }

.badge:enabled.selected:focus {
                background-color: rgba(212, 181, 139, 0.8);
            }

.badge:disabled {
        border-color: rgba(51, 74, 67, 0.2);
        background-color: transparent;
        color: #4b453d
    }

.badge:disabled span {
            opacity: 0.5;
        }
