  Note: If you add a color here, please add it to "/stories/base/colors.css" as well
*/

.container {
    position: relative;
    width: 6rem;
    height: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 1.6rem;
    font-size: 2.5rem
}

.container:hover .remove {
        opacity: 1;
        pointer-events: auto;
    }

.remove {

    position: absolute;
    top: 0;
    right: 0;
    width: 27%;
    height: 27%;
    transform: translate3d(27%, -27%, 0);
    opacity: 0;
    pointer-events: none;
    cursor: pointer;
    transition: opacity 0.15s ease
}

.remove > svg {
        width: 100%;
        height: 100%;
        fill: #d0021b;
    }

.content {
    width: 2.5rem;
    height: 2.5rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center
}

.content > * {
        width: 100%;
        height: 100%;
    }
