.svgWrapper {
    display: inline-block;
    vertical-align: middle;
    fill: currentColor
}
.svgWrapper svg {
        width: inherit;
        height: inherit;
        display: block;  /* Remove little margin between the svg and its parent */
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none;
    }
